@import "../../utils/colors.css";

.aboutContainer {
  z-index: 1;
  display: flex;
  flex-direction: column;
  color: var(--light1);
  margin: 0 6rem 0 6rem;
}

.avatar {
  z-index: 1;
  align-self: center;
  width: 25%;
  height: auto;
  margin-bottom: 1rem;
}

.welcomeHeader,
.brandStatement,
.skillsetHeader h3 {
  z-index: 1;
}

.skillsContainer {
  color: var(--accent);
  margin: 6rem;
  z-index: 1;
}
.aboutContainer h3,
.skillsContainer h3 {
  font-size: 3rem;
}

.skillsContainer h3 {
  font-family: "Petit Formal Script";
  text-align: center;
}

.aboutContainer p {
  font-size: 1.5rem;
  margin-top: 3%;
}

.iconsDiv {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.icon {
  color: var(--accent);
  font-size: 3rem;
  margin: 3rem;
}

@media only screen and (max-width: 600px) {
  .welcome {
    flex-direction: column;
  }
  /* .aboutContainer h3,
  .skillsContainer h3 {
    font-size: 3rem;
  }
  .aboutContainer p,
  .skillsContainer p {
    font-size: 1rem;
    margin-top: 3%;
  } */
  .iconsDiv {
    flex-wrap: wrap;
  }
}
